import toast from './toast';
const t = toast({
    delay: 5000
});
export { t as toast };
export const debounce = (func, wait) => {
    let timer = null;
    return (...args) => {
        if (timer) {
            clearTimeout(timer);
        }
        timer = setTimeout(() => {
            func(...args);
        }, wait);
    };
};



